footer {
  position: relative;
  bottom: 0;
  padding: 10px;

  background: var(--dark-background)
}

.info_box {
  padding: 10px;
}

.info_box .social {
  display: flex;
  align-items: center;
}

footer h1 {
  font-size: 30px;
}
footer h2 {
  font-size: 20px;
}

footer a {
  text-decoration: none;
  color: #cccccc;
}
.icon {
  font-size: 20pt;
}
.contact {
  font-size: 14pt;
}

.copy a {
  color: #e7e7e750;
}
